// A mock function to mimic making an async request for data
export function fetchLoggedInUserOrders() { 
  return new Promise(async(resolve) => { // here we used promise
    // const response = await fetch(`http://localhost:8080/orders/?user=`+userId);   // old
    const response = await fetch(`http://localhost:8080/orders/own/`);
    const data = await response.json();
    resolve({data});
  }
  );
}

export function fetchLoggedInUser() { 
  return new Promise(async(resolve) => { // here we used promise
    const response = await fetch(`http://localhost:8080/users/own`);
    const data = await response.json();
    resolve({data});
  }
  );
}


export function updateUser(update) { 
  return new Promise(async(resolve) => { // here we used promise
    const response = await fetch('http://localhost:8080/users/'+update.id ,{
      method: 'PATCH',
      body: JSON.stringify(update),
      headers:{'content-type':'application/json'}
    });
    const data = await response.json();
    // ToDo: On server it will return some information (not passsword)
    resolve({data});
  }
  );
}